		color: $orange;
		margin: 10px 0;

		span {
			background-color: rgba(53, 66, 83, 0.1);
			padding: 5px;

			small {
				color: $marron;
			}
		}
	}

	.posts {
		.post {
			.post-title {
				font-weight: bold;
				font-size: 32px;
				font-family: $titleFontPrimary;
				color: $orange;
			}
			.post-content {
				color: $marron;
			}

		}
	}
}

.appel-mobile {
	.heading {
		margin: 0 0 40px;

		.title {
			font-family: $titleFontPrimary;
			font-weight: bold;
			line-height: 1;
			font-size: 42px;
			color: $orange;
		}
		.subtitle {
			line-height: 1.1;
			padding: 4px;
			font-weight: 600;
			background-color: $orange;
			font-size: 28px;
			width: 360px;
			color: $white;
			max-width: 100%;
		}
	}

	.content-letter {
		padding: 10px 10px 40px;

		.btn-don {
			display: block;
			width: 500px;
			max-width: 100%;
			margin: 30px auto;
			text-transform: uppercase;
			font-size: 26px;
		}
		p {
			color: $marron;
			font-size: 18px;
		}
		.name {
			color: $primary;
			font-family: $bodyFontSecondary;
			font-weight: 600;
			margin: 0;
		}
	}
}
